                @click="dd.available && $emit('select', index2)"
              >fa-map-marker-alt</v-icon>
            </div>
          </div>
        </div>
      </div>
    </v-col>
  </v-row>
</template>

<script>
export default {
  props: {
    rows: { type: Array, required: true },
    players: { type: Array, required: true },
    itemKey: { type: Number, required: true },
    cols: { type: Number, required: true },
  },
};
