import { ROWS } from './ludo.constant';

export default {
  onDiceChange(value) {
    this.dice = value;
    this.setAvailable(false);

    if (this.dice == 6) {
      this.setAvailable(true, this.activePlayer);
    } else {
      this.setAvailable(true, this.activePlayer, true);
    }
  },
  onDiceChange1(index) {
    this.setAvailable(false);

    const player = this.players[this.activePlayer];
    const item = player.items[index];
    // player.isActive = true;

    if (!item.value) {
      if (this.dice == 6) {
        item.value = 1;
      }

      this.onDiceChangeCallback();
      return;
    }

    this.reduceDramatically(
      this.dice,
      250,
      () => {
        item.value++;
        this.key++;
      },
      () => {
        const players = this.players.filter((_player, index) => {
          return (
            index != this.activePlayer &&
            item.value &&
            _player.items.some((_item) => {
              return (
                _item.value &&
                item.value == _item.value + this.diff[player.id][_player.id]
              );
            })
          );
        });

        if (players.length) {
          players.forEach((player, index) => {
            this.reduceDramatically(
              item.value,
              100,
              () => {
                item.value = item.value - 2;
                if (item.value == 0) {
                  item.value = null;
                }
                this.key++;
              },
              () => {
                this.onDiceChangeCallback();
              }
            );
          });
        } else {
          this.onDiceChangeCallback();
        }
      }
    );
  },
  onDiceChangeCallback() {
    if (this.dice != 6) {
      if (this.activePlayer == 1) {
        this.activePlayer = 3;
      } else if (this.activePlayer == 2) {
        this.activePlayer = 0;
      } else if (this.activePlayer == 3) {
        this.activePlayer = 2;
      } else {
        this.activePlayer = 1;
      }
    }

    // setTimeout(() =>  {
    //   this.$refs.dice.onClick();
    // }, 1000);
  },
  reduceDramatically(count, speed, iterator, cb) {
    // if (!speed) return cb();

    const interval = setInterval(function() {
      if (count == 0) {
        clearInterval(interval);
        if (cb) cb();
      } else {
        count--;
        iterator();
      }
    }, speed);
  },
  setAvailable(boolean, index, value) {
    const closure = (player) => {
      player.items.forEach((item) => {
        if (!value || item.value) {
          item.available = boolean;
        }
      });
    };

    if (index || index == 0) {
      const player = this.players[index];
      closure(player);
    } else {
      this.players.forEach(closure);
    }
  },
  setRows() {
    this.rows = ROWS;

    this.history = [];
    this.activeBall = 1;
    this.activeColumn = null;

    this.copyRows = _.cloneDeep(this.rows);
  },
};
