<template>
  <div class="cursor-pointer" @click="onClick()">
    <v-icon style="font-size:5em;">fa-dice-{{classNames[inputValue]}}</v-icon>
  </div>
</template>

<script>
export default {
  name: 'Dice',
  props: {
    // value: {type:number,req }
  },
  data() {
    return {
      inputValue: this.value,
      classNames: {
        1: 'one',
        2: 'two',
        3: 'three',
        4: 'four',
        5: 'five',
        6: 'six',
      },
    };
  },
  methods: {
    setValue() {
      this.inputValue = Math.floor(Math.random() * 6) + 1;
    },
    onClick() {
      this.setValue();
      this.$emit('change', this.inputValue);
    },
  },

  created() {
    this.setValue();
  },
};
</script>